import LightGallery from 'lightgallery/react';

// import styles
import 'lightgallery/css/lightgallery.css';
import 'lightgallery/css/lg-zoom.css';
import 'lightgallery/css/lg-thumbnail.css';

// If you want you can use SCSS instead of css
import 'lightgallery/scss/lightgallery.scss';
import 'lightgallery/scss/lg-zoom.scss';

// import plugins if you need
import lgThumbnail from 'lightgallery/plugins/thumbnail';
import lgZoom from 'lightgallery/plugins/zoom';
import lgVideo from 'lightgallery/plugins/video';
import lgToolbar from 'lightgallery/plugins/autoplay';

export default function Gallery(props) {
    return (
        <div >
            
            <LightGallery
          plugins={[lgZoom, lgVideo,lgThumbnail,lgToolbar]}
          mode="lg-fade"
          closeOnTap={false}
          download={true}
          showCloseIcon={true}
          onInit={() => {
            console.log(props.imgList);
          }}
        >
        {props.imgList.map((v,ind)=>
            <a
            className="gallery-item lightRoomImgElement"
            key={ind}
            data-src={v}
            // data-sub-html="<p>Test</p>"
          >
            <img
              className="img-responsive"
              src={v}
            />
          </a>
        )}
          
        </LightGallery>
        </div>
    );
}
