import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import DeleteIcon from '@mui/icons-material/Delete';
import IconButton from '@mui/material/IconButton';
import Checkbox from '@mui/material/Checkbox';
import GroupAddIcon from '@mui/icons-material/GroupAdd';
import Avatar from '@mui/material/Avatar';
import Divider from '@mui/material/Divider';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import { styled } from '@mui/material/styles';

import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import FirebaseContext from '../../firebaseContext';
export default function UserList(props) {
  let fbCtxt = React.useContext(FirebaseContext);
  React.useEffect(()=>{
    console.log("Setting Numbers",props.selectedAlbum);
    setViewerList(props.selectedAlbum.users);
  },[props.selectedAlbum.users]);
  const [username, setUsername] = React.useState('');
  const [phoneNum, setPhoneNum] = React.useState('');
  const [viewerList, setViewerList] = React.useState([]);

  const saveCurrentAlbumUsrToDb = (lst)=>{
    console.log("saving => to db => ",props.selectedAlbum,lst);
    fbCtxt.updateAlbum(props.selectedAlbum,lst);
  }
  const addUserBtnClk = () => {
    // console.log(viewerList);
    fbCtxt.setLoading(true);
    if(username && phoneNum && username!="" && phoneNum!=""){
      setViewerList((prev)=>{
        let tmpViewers = [{message:username,phone:phoneNum},...prev];
        saveCurrentAlbumUsrToDb(tmpViewers);
        return(tmpViewers);});
      setUsername("");
      setPhoneNum("");
    }else if(username!=""){
      alert("User Name Invalid");
    }else if(phoneNum!=""){
      alert("User Name Invalid");
    }
  }

  const removeItem = (user) => {
    fbCtxt.setLoading(true);
    setViewerList((prev)=>{
      let dataAfterPop = prev.filter((usrObj)=>{
        if(usrObj.message==user.message && usrObj.phone == user.phone){
          return(false);
        }else{
          return(true);
        }
      });
      saveCurrentAlbumUsrToDb(dataAfterPop);
      return(dataAfterPop);
    });
  }

  const handleChange = (event) => {
    console.log("Dropdown Clicked");
  };

  return (
    <Paper sx={{ maxWidth: 936, margin: 'auto', overflow: 'hidden' }} className="bshadow">
    <AppBar
      position="static"
      color="default"
      elevation={0}
      sx={{ borderBottom: '1px solid rgba(0, 0, 0, 0.12)' }}
    >
      <Toolbar>
        <Grid container spacing={2} alignItems="center">
          <Grid item>
            <GroupAddIcon color="inherit" sx={{ display: 'block' }} />
          </Grid>
          <Grid item xs>
            <TextField
              fullWidth
              placeholder="Enter User Name"
              InputProps={{
                disableUnderline: true,
                sx: { fontSize: 'default' },
              }}
              variant="standard"
              value={username}
              onChange={(e)=>setUsername(e.target.value)}
            />
          </Grid>
        </Grid>
      </Toolbar>
    </AppBar>
      <AppBar
        position="static"
        color="default"
        elevation={0}
        sx={{ borderBottom: '1px solid rgba(0, 0, 0, 0.12)' }}
      >
        <Toolbar>
          <Grid container spacing={2} alignItems="center">
            <Grid item>
              <PhoneInTalkIcon color="inherit" sx={{ display: 'block' }} />
            </Grid>
            <Grid item xs>
              <TextField
                fullWidth
                placeholder="Phone Number"
                InputProps={{
                  disableUnderline: true,
                  sx: { fontSize: 'default' },
                }}
                variant="standard"
                type="number"
                value={phoneNum}
                onChange={(e)=>setPhoneNum(e.target.value?.replaceAll(" ",""))}
              />
            </Grid>
            <Grid item>
              <Button variant="contained" sx={{ mr: 1 }} onClick={addUserBtnClk}>
                Add User
              </Button>
              {/* <Tooltip title="Reload">
                <IconButton>
                  <RefreshIcon color="inherit" sx={{ display: 'block' }} />
                </IconButton>
              </Tooltip> */}
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      <AppBar
        position="static"
        color="default"
        elevation={0}
        sx={{ borderBottom: '1px solid rgba(0, 0, 0, 0.12)' }}
      >
      <Toolbar>
          <Grid container spacing={2} alignItems="center">
            <Grid item alignItems="center" width={"100%"}>
              <br/>
                        
              <Box sx={{ minWidth: 120 }} width="100%">
                <FormControl fullWidth>
                  <InputLabel id="demo-simple-select-label">Selected Album</InputLabel>
                  <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    value={props.selectedAlbum.name}
                    label="Selected Album"
                    onChange={handleChange}
                  >
                    <MenuItem value={props.selectedAlbum.name}>{props.selectedAlbum.name}</MenuItem>
                  </Select>
                </FormControl>
              </Box>
              {/* <Typography variant="h6" component="h2">{props.selectedAlbum.name}</Typography> */}
              <Typography mt={1} className="ownedBy">Owned by {props.selectedAlbum.owner}</Typography>
              <hr />
              <Typography mb={1} variant="h6">Access Manager</Typography>
            </Grid>
            </Grid>
      </Toolbar>
      </AppBar>
      {/* <Typography sx={{ my: 5, mx: 2 }} color="text.secondary" align="center">
        No results matching your search...
      </Typography> */}
<Grid container spacing={2} padding="3%">
    {/* <Box sx={{ height: 450, overflowY: 'scroll' }}>
      <ImageList variant="masonry" cols={3} gap={8}>
        {jsonData['list'].map((data,index) => (
          <ImageListItem key={index}>
           <ExpanCard data={data}></ExpanCard>
          </ImageListItem>
        ))}
      </ImageList>
    </Box> */}



      <Grid item sm={12} md={12} width="100%">
        
    <List dense sx={{ width: '100%', bgcolor: 'background.paper' }}>
      {viewerList && viewerList.length<=1 &&<div style={{margin:"0% 2%"}}>No viewers available, add viewers with the form above.</div>}
      {viewerList.map((users,index) => {
        if(props.selectedAlbum.owner==users.phone){return("");}
        const labelId = `checkbox-list-secondary-label-${users.phone}`;
        return (
            <span key={users.phone+index}>
                
          <ListItem
            secondaryAction={
              <IconButton edge="end" aria-label="delete" onClick={()=>{removeItem(users);}}>
                <DeleteIcon />
              </IconButton>
            }
            disablePadding
          >
            <ListItemButton>
              <ListItemAvatar 
                  onClick={()=>{window.open(
                    "https://api.whatsapp.com/send?phone=91"+users.phone+"&text=Hi%2C%0AThank%20you%20for%20adding%20to%20the%20joyful%20spirit%20of%20our%20event%20with%20your%20presence.%0A%0ALogin%20with%20"+users.phone+"%20to%C2%A0view%C2%A0the%C2%A0photos%0A%0Ahttps%3A%2F%2Fepicbooksview.github.io%2F%3Fstudio%3D"+props.selectedAlbum.name
                    ,"_blank")}}>
                <Avatar
                  alt={users.message.substring(0,1)}
                  src="img/wa.svg"
                />
              </ListItemAvatar>
              <ListItemText id={labelId} primary={users.phone}
              secondary={users.message} />
            </ListItemButton>
          </ListItem>
            <Divider variant="inset" component="li" />
            </span>
        );
      })}
    </List>
      </Grid>
</Grid>
    </Paper>


  );
}
