const drawerWidth = 256;

export default function Paperbase() {
  const fbCtx = React.useContext(FirebaseContext);
  const [mobileOpen, setMobileOpen] = React.useState(false);
  const [selectedAlbum, setSelectedAlbum] = React.useState();
  const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

  const [ currentPage, setCurrentPage ] = React.useState(0);
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  React.useEffect(()=>{
    console.log("CURRENT PAGE CHANGED< SCROLLING TO TOP");
    window.scrollTo(0, 0);
  },[currentPage]);

  React.useEffect(()=>{
    console.log("ALBUM CHANGED UPDATING SELECTED ALBUM",fbCtx.albums.list);
    // console.log("selecedId ===>>>> ",localStorage.getItem("selecedId"));
    if(fbCtx && fbCtx.albums && fbCtx.albums.list){
      fbCtx.albums.list.forEach((v)=>{
        console.log(v.docId,"+++++",localStorage.getItem("selecedId"));
        if(v.docId==localStorage.getItem("selecedId")){
          setSelectedAlbum(v);
          console.log("Info : ALBUMS SELECTED AND SET WITH ID...",v);
        }
      });
    }else{
      console.log("Info : NO ALBUMS SELECTED...");
    }
  },[fbCtx.albums]);

  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ display: 'flex', minHeight: '100vh' }}>
        <CssBaseline />
        <Box
          style={{zIndex:1000}}
          component="nav"
          sx={{ width: { sm: drawerWidth }, flexShrink: { sm: 0 } }}
        >
          {isSmUp ? null : (
            <Navigator
              PaperProps={{ style: { width: drawerWidth } }}
              variant="temporary"
              open={mobileOpen}
              onClose={handleDrawerToggle}
            />
          )}

          <Navigator
            PaperProps={{ style: { width: drawerWidth } }}
            sx={{ display: { sm: 'block', xs: 'none' } }}
          />
        </Box>
        <Box sx={{ flex: 1, display: 'flex', flexDirection: 'column' }}>
          <Header className="bgTrans" onDrawerToggle={handleDrawerToggle} selectedAlbum={selectedAlbum}  currentPage={currentPage} setCurrentPage={setCurrentPage}/>
          <Box component="main" sx={{ flex: 1, py: 1, px: 1.5, bgcolor: '#eaeff1' }} >
          {(currentPage===0)?<Content currentPageSetter={setCurrentPage} setSelectedAlbum={setSelectedAlbum}  />:""}
          {(currentPage===1)?<ViewContent selectedAlbum={selectedAlbum} currentPageSetter={setCurrentPage} />:""}
          {(currentPage===2)?<UserList selectedAlbum={selectedAlbum} />:""}
          </Box>
          <Box component="footer" sx={{ p: 2, bgcolor: '#eaeff1' }}>
            <Copyright />
          </Box>
        </Box>
      </Box>
    </ThemeProvider>
  );
}
