import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import PhotoCameraBackIcon from '@mui/icons-material/PhotoCameraBack';
import VideoCameraBackIcon from '@mui/icons-material/VideoCameraBack';
import VideoSettingsIcon from '@mui/icons-material/VideoSettings';
import Box from '@mui/material/Box';
import PreviewIcon from '@mui/icons-material/Preview';
import ImgView from './components/imgView';
import VidView from './components/vidView';
import LanguageIcon from '@mui/icons-material/Language';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import IconButton from '@mui/material/IconButton';
import { Pagination } from '@mui/material';
import { adminNum, carPerPage } from '../constants';
import Gallery from './adminComps/Gallery';
import FitScreenIcon from '@mui/icons-material/FitScreen';
import ForwardTwoToneIcon from '@mui/icons-material/ForwardTwoTone';

export default function ViewContent(props) {
  const [pageCount, setPageCount] = React.useState(1);
  const [pageCur, setPageCur] = React.useState(1);
  const [viewButtonType,setViewType] = React.useState("videos");
  let itemPerPage = carPerPage;
  const [startPg, setStartPg] = React.useState(0);
  const [endPg, setEndPg] = React.useState(itemPerPage);
  React.useEffect(()=>{
    setStartPg(0);
    setEndPg(itemPerPage);
    let lenOfImgList = 1;
    if(viewButtonType!=="photos"){
    lenOfImgList = Math.ceil(props.selectedAlbum.images.length/itemPerPage);
    console.log(props.selectedAlbum.images.length,"Pagination Reset",lenOfImgList);
  }else{
    lenOfImgList = Math.ceil(props.selectedAlbum.videos.length/itemPerPage);
    console.log(props.selectedAlbum.videos.length,"Pagination Reset",lenOfImgList);
  }
    setPageCount(lenOfImgList);
    setPageCur(1);
  },[viewButtonType]);
  const changeView = ()=>{
    (viewButtonType==="photos")?(setViewType("videos")):(setViewType("photos"));
    console.log("CLICKED");
  };
  const changeViewToManageV = ()=>{
    props.currentPageSetter(2);
    console.log("CLICKED");
  };
  const studioLink = ()=>{
    alert("Opening Studio's Website");
    window.open(props.selectedAlbum.website, '_blank');
  };
  const handlePageChange=(e,v)=>{
    setPageCur(v);
    console.log(e,(v));
    setStartPg(((v*itemPerPage)-itemPerPage));
    setEndPg((v*itemPerPage));
    window.scrollTo(0, 0);
  }
  function lightroomView(val){
    console.log("Lightroom Called++++++++++++++=========++++++++++++++++",document.getElementsByClassName("lightRoomImgElement"));
      const clickEvent = new MouseEvent("click", {
          view: window,
          bubbles: true,
          cancelable: false,
        });
        if(document.getElementsByClassName("lightRoomImgElement") && document.getElementsByClassName("lightRoomImgElement").length>0){
          document.getElementsByClassName("lightRoomImgElement")[val].dispatchEvent(clickEvent);
        }
  }
  return (
    <Paper sx={{ maxWidth: 936, margin: 'auto', overflow: 'hidden' }} className="bshadow">
      <Card sx={{ display: 'flex' }} className="titleSection">
      <Box sx={{ display: 'flex', flexDirection: 'column' }} 
      // width="75%"
      >
        <CardContent sx={{ flex: '1 0 auto' }} className="zeroBotPadding">
          <Typography className='topBarstyle titleHeadText' component="div" variant="h5">
            {props.selectedAlbum.name}
          </Typography>
          {/* <Typography className='topBarstyle' variant="subtitle1" color="text.secondary" component="div">
            {props.selectedAlbum.date}&nbsp;
          </Typography> */}
          <Typography className='topBarstyle' variant="subtitle1" color="text.secondary" component="div">
          From {props.selectedAlbum.owner}. {props.selectedAlbum.fullDesc.substring(0,36)}..
          </Typography>
        </CardContent>
        
        {/* <Box className='studioLink topBarstyle' sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }} onClick={()=>{lightroomView(0)}}>
          <IconButton aria-label="play/pause">
            <FitScreenIcon sx={{ height: 28, width: 28 }} />
          </IconButton>
          Full Screen View
        </Box> */}
        {
        ((props.selectedAlbum.owner.split(',').includes(window.localStorage.getItem("loginPhone")))
        || (window.localStorage.getItem("loginPhone")===adminNum))
        ?(<Box className='studioLink topBarstyle' sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }} onClick={changeViewToManageV}>
          <IconButton aria-label="access">
            <PreviewIcon sx={{ height: 28, width: 28 }} />
          </IconButton>
          Access Manager
        </Box>):("")}
        <Box className='studioLink topBarstyle' sx={{ display: 'flex', alignItems: 'center', pl: 1, pb: 1 }} onClick={studioLink}>
          <IconButton aria-label="play/pause">
            <LanguageIcon sx={{ height: 28, width: 28 }} />
          </IconButton>
          {props.selectedAlbum.studio}
        </Box>
      </Box>
      <span style={{display:"none"}}>
      <Gallery imgList={props.selectedAlbum.images}></Gallery>
      </span>
      {/* <CardMedia
        component="img"
        sx={{ width: "25%" }}
        image={props.selectedAlbum.image}
        className="titleImg"
        alt="Live from space album cover"
      /> */}
      
    </Card>
      <AppBar
        position="static"
        color="default"
        elevation={0}
        sx={{ borderBottom: '1px solid rgba(0, 0, 0, 0.12)' }}
      >
        <Toolbar>
          <Grid container spacing={2} alignItems="center">
          {/* {(viewButtonType!=="photos")?
              (
                <Grid item >
                <PhotoCameraBackIcon className='topBarstyleIcon' color="inherit" sx={{ display: 'block' }} />
              </Grid>
              ):(
                <Grid item >
                <VideoCameraBackIcon className='topBarstyleIcon' color="inherit" sx={{ display: 'block' }} />
              </Grid>
              )} */}
            
            {/* {(viewButtonType!=="photos")?
              (
              <Grid item className='topBarstyle'>
                Photos 
              </Grid>
              ):(
                <Grid item className='topBarstyle'>
                  Videos 
                </Grid>
              )} */}
              <Grid item className='topBarstyle'>
                Photos 
              </Grid>

            <Grid item marginLeft='auto'>
                
            {/* {(viewButtonType==="photos")?
              (
                <Button variant="outlined" className='btnStyleRefresh greyBorderBtn ' size="small" onClick={changeView}>
                  <PhotoCameraBackIcon color="inherit" sx={{ display: 'block' }} />
                    &nbsp;&nbsp;Photos
                </Button>
              ):(
                <Button variant="outlined" className='btnStyleRefresh greyBorderBtn ' size="small" onClick={changeView}>
                  <VideoCameraBackIcon color="inherit" sx={{ display: 'block' }} />
                    &nbsp;&nbsp;Videos
                </Button>
              )}&nbsp; */}
              
              <Button variant="outlined" className='btnStyleRefresh greyBorderBtn ' size="small" onClick={()=>{lightroomView(startPg)}}>
                  <FitScreenIcon color="inherit" sx={{ display: 'block' }} />
                    &nbsp;&nbsp;Full Screen
                </Button>
              
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      {/* <Typography sx={{ my: 5, mx: 2 }} color="text.secondary" align="center">
        No results matching your search...
      </Typography> */}
<Grid container spacing={2} padding="3%" style={{paddingTop: "17px"}}>
    {/* <Box sx={{ height: 450, overflowY: 'scroll' }}>
      <ImageList variant="masonry" cols={3} gap={8}>
        {jsonData['list'].map((data,index) => (
          <ImageListItem key={index}>
           <ExpanCard data={data}></ExpanCard>
          </ImageListItem>
        ))}
      </ImageList>
    </Box> */}



  {
    (viewButtonType!=="photos")?(
    props.selectedAlbum.images.slice(startPg,endPg).map((data,index)=>{
    return(
      <Grid item sm={12} md={4} key={index} width="100%" className='viewThumbDiv'>
        <ImgView data={props.selectedAlbum} imgData={data}></ImgView>
      </Grid>
    );
  })
    ):(
      props.selectedAlbum.videos.slice(startPg,endPg).map((data,index)=>{
        return(
          <Grid item sm={12} md={4} key={index} width="100%" className='viewThumbDiv'>
           <VidView data={data}></VidView> 
          </Grid>
        );
      })
    )
  }
  {
        (((viewButtonType==="photos") && props.selectedAlbum.videos && props.selectedAlbum.videos.length==0)?
        <div className='textCntr txtDivCntr'>
          <p>
            No videos available
          </p>
        </div>
        :"")
  }
</Grid>
<Grid width="100%">
  <Pagination style={{margin:"5% auto", width:"fit-content"}} 
      count={pageCount} onChange={handlePageChange} page={pageCur} color="primary" />
</Grid>
<Grid width="100%" className='nextButton'>
<Button variant="outlined" className='btnStyleRefresh greyBorderBtn boldTxt' size="large" onClick={()=>{handlePageChange({},pageCur+1)}}>
                  &nbsp;Next&nbsp;&nbsp;<ForwardTwoToneIcon color="inherit" sx={{ display: 'block' }} />
                </Button>
</Grid>
    </Paper>
  );
}
