import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import SearchIcon from '@mui/icons-material/Search';
import ExpanCard from './components/ExpanCard';
import FirebaseContext from '../firebaseContext';
import { useNavigate } from 'react-router-dom';

export default function Content(props) {
  const [searchTxt,setSearchTxt] = React.useState("");  
  const [albumArr,setAlbumArr] = React.useState([]);
  let navigate = useNavigate();
  const fbCtxt = React.useContext(FirebaseContext);
  React.useEffect(()=>{
    console.log("contentJs fbCtxt.albums UseEffect Ran",fbCtxt.albums);
    let arrValue = fbCtxt.albums.list.slice();
    setAlbumArr(arrValue.reverse());
    if(!window.localStorage.getItem("loginPhone")){
      navigate("/");
    }
  },[fbCtxt.albums]);
  function onSearchClick(searchTxt){
    console.log(searchTxt);
    if(searchTxt!=""){
      let arrValue = fbCtxt.albums.list.slice();
      let tmpVar = arrValue.reverse().filter((data)=>{
        if(data.name.toLocaleLowerCase().includes(searchTxt.toLocaleLowerCase())){return true;}
        if(data.fullDesc.toLocaleLowerCase().includes(searchTxt.toLocaleLowerCase())){return true;}
        if(data.studio.toLocaleLowerCase().includes(searchTxt.toLocaleLowerCase())){return true;}
      });
      setAlbumArr(tmpVar);
    }
    else{setAlbumArr(fbCtxt.albums.list.slice());}
  }
  return (
    <Paper sx={{ maxWidth: 936, margin: 'auto', overflow: 'hidden' }} className="bshadow">
      <AppBar
        position="static"
        color="default"
        elevation={0}
        sx={{ borderBottom: '1px solid rgba(0, 0, 0, 0.12)' }}
      >
        <Toolbar>
          <Grid container spacing={2} alignItems="center">
            <Grid item>
              <SearchIcon color="inherit" sx={{ display: 'block' }} />
            </Grid>
            <Grid item xs>
              <TextField
                fullWidth
                placeholder="Search Album"
                InputProps={{
                  disableUnderline: true,
                  sx: { fontSize: 'default' },
                }}
                variant="standard"
                value={searchTxt}
                onChange={(e)=>{setSearchTxt(e.target.value);onSearchClick(e.target.value);}}
              />
            </Grid>
            <Grid item>
              <Button className="btnStyleRefresh" variant="contained" sx={{ mr: 1 }} onClick={()=>fbCtxt.fetchAlbum()}>
                Refresh
              </Button>
              {/* <Tooltip title="Reload">
                <IconButton>
                  <RefreshIcon color="inherit" sx={{ display: 'block' }} />
                </IconButton>
              </Tooltip> */}
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
      {/* <Typography sx={{ my: 5, mx: 2 }} color="text.secondary" align="center">
        No results matching your search...
      </Typography> */}
      
  {
    (albumArr.length==0)?
            <Grid container spacing={2} padding="3%" width={"100%"}>
              <Typography sx={{ my: 5, mx: 2 }} color="text.secondary" width={"100%"} align="center">
                No results matching your search
              </Typography>
              <img width="100%" src="img/empty.jpg"/>
            </Grid>:""
  }
<Grid container spacing={2} padding="3%">
    {/* <Box sx={{ height: 450, overflowY: 'scroll' }}>
      <ImageList variant="masonry" cols={3} gap={8}>
        {jsonData['list'].map((data,index) => (
          <ImageListItem key={index}>
           <ExpanCard data={data}></ExpanCard>
          </ImageListItem>
        ))}
      </ImageList>
    </Box> */}


  {
  albumArr.map((data,index)=>{
    return(
      <Grid item sm={12} md={4} key={index} width="100%">
        <ExpanCard data={data} currentPageSetter={props.currentPageSetter} setSelectedAlbum={props.setSelectedAlbum}></ExpanCard>
      </Grid>
    );
  })
  }
</Grid>
    </Paper>
  );
}
