import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import Avatar from '@mui/material/Avatar';
import ScreenShareIcon from '@mui/icons-material/ScreenShare';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { red } from '@mui/material/colors';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandCircleDownIcon from '@mui/icons-material/ExpandCircleDown';
import InstagramIcon from '@mui/icons-material/Instagram';
import YouTubeIcon from '@mui/icons-material/YouTube';

const ExpandMoreRight = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));
const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));

export default function ExpanCard(props) {
  const [expanded, setExpanded] = React.useState(false);
  const [imgLoaded, setImgLoaded] = React.useState(false);
  const [imgLogoLoaded, setImgLogoLoaded] = React.useState(false);
  const handleExpandClick = () => {
    setExpanded(!expanded);
  };
  const openAlbum = (a)=>{
    console.log(a);
    props.setSelectedAlbum(a);
    props.currentPageSetter(1);
    window.localStorage.setItem("selecedId",a.docId);
  }
  

  return (
    <Card className='smallIndesk' variant="outlined" >

      <CardHeader
        avatar={
          <Avatar sx={{ bgcolor: red[500] }} aria-label="recipe" >
            {props.data.name.substring(0, 1)}
          </Avatar>
        }
        action={
          <IconButton aria-label="settings">
            <ScreenShareIcon />
          </IconButton>
        }
        title={props.data.name}
        subheader={props.data.date}
        onClick={()=>openAlbum(props.data)}
      />
      <CardMedia
        component="img"
        height="250"
        image={props.data.image}
        alt="img of album"
        style={{display: `${!imgLoaded?"none":""}`}}
        onClick={()=>openAlbum(props.data)}
        onLoad={()=>{console.log("Image loaded"); setImgLoaded(true)}}
      />
      {(!imgLoaded && 
      // <CardMedia
      //   component="img"
      //   width="100%"
      //   image="img/loading.svg"
      // />
      // https://icons8.com/preloaders/en/free
      <svg width="100%" height="200px" className="loadingAnime" viewBox="0 0 200 200"  version="1.1"
      xmlns="http://www.w3.org/2000/svg">
         <image x="60" y="60" width="80" height="80" href="img/load2.svg" />
       </svg>
      )}
      <CardContent>
        
      <Typography variant="body2" color="text.secondary">
          {/* {props.data.fullDesc.substring(0,80)}... */}
          {props.data && props.data.owner===window.localStorage.getItem("loginPhone") && 
          <React.Fragment>Album owned by <strong>You</strong>. Click below for more details.</React.Fragment>
          }
          
          {props.data && props.data.owner!==window.localStorage.getItem("loginPhone") && 
          <React.Fragment>Album owned by <strong>{props.data.owner}</strong>. Click below for more details.</React.Fragment>
          }
      </Typography>
      </CardContent>
      
      <CardActions onClick={handleExpandClick} disableSpacing>
        
      <ExpandMore
          expand={expanded}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandCircleDownIcon />
        </ExpandMore>
        
        <span className='moreDropdown'>More ( {props.data.studio} )</span>
        {/* <IconButton aria-label="share">
          <ShareIcon />
        </IconButton> */}
        <ExpandMoreRight
          expand={expanded}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </ExpandMoreRight>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
      {/* {(props.data.owner!=window.localStorage.getItem("loginPhone")?
        ( 
      <div className='lr_margin'>
          <Paper elevation={3}  style={{backgroundColor:"#ffd2d4"}}  >
          <Grid item>
            <IconButton aria-label="share">
              <DeleteForeverIcon />
            </IconButton> 
            <span className='moreDropdown'>Delete Album</span>
            <IconButton aria-label="share" className='floatRight'>
              <NavigateNextIcon />
            </IconButton> 
          </Grid>
        </Paper>
        </div>
        ):"")} */}
        
        {props.data.studioLogo && props.data.studioLogo!=="" && 
          <img
          width="90%"
          style={{margin:"1% 5%",display: `${!imgLogoLoaded?"none":""}`}}
          src={props.data.studioLogo}
          alt="img of album"
          onLoad={()=>{console.log("Logo Image loaded"); setImgLogoLoaded(true);}}
        />}
        
        {(props.data.studioLogo && props.data.studioLogo!=="" && !imgLogoLoaded && 
        // <img 
        //   width="100%"
        //   src="img/loading.svg"
        // />
      <svg width="100%" height="200px" className="loadingAnime" viewBox="0 0 200 200"  version="1.1"
      xmlns="http://www.w3.org/2000/svg">
         <image x="60" y="60" width="80" height="80" href="img/load2.svg" />
       </svg>
        )}

 {(props.data.website!==""?
        (
          <div className='lr_margin'>
          <Paper elevation={3} onClick={()=>{window.open(props.data.website,"_blank")}}>
          <Grid item>
            <IconButton aria-label="share">
              <InstagramIcon />
            </IconButton> 
            <span className='moreDropdown'>Website ({props.data.website?.substring(0,10)}..)</span>
            <IconButton aria-label="share" className='floatRight'>
              <NavigateNextIcon />
            </IconButton> 
          </Grid>
        </Paper>
        </div>
        ):
        ("")
        )}
 {(props.data.instagram!==""?
        (
          <div className='lr_margin'>
          <Paper elevation={3} >
          <Grid item>
            <IconButton aria-label="share">
              <InstagramIcon />
            </IconButton> 
            <span className='moreDropdown'>Instagram ({props.data.instagramId?.substring(0,10)}..)</span>
            <IconButton aria-label="share" className='floatRight'>
              <NavigateNextIcon />
            </IconButton> 
          </Grid>
        </Paper>
        </div>
        ):
        ("")
        )}

{(props.data.youtube!==""?
        (
          <div className='lr_margin'>
          <Paper elevation={3} >
          <Grid item>
            <IconButton aria-label="share">
              <YouTubeIcon />
            </IconButton> 
            <span className='moreDropdown'>Youtube ({props.data.youtubeId?.substring(0,10)}..)</span>
            <IconButton aria-label="share" className='floatRight'>
              <NavigateNextIcon />
            </IconButton> 
          </Grid>
        </Paper>
        </div>
        ):
        ("")
        )}
        {
          props.data.studioDetails &&
          props.data.studioDetails !=="" && <CardContent>
          <Typography paragraph>Studio Details:</Typography>
          <Typography paragraph>
            {props.data.studioDetails}
          </Typography>
        </CardContent>
          }
      </Collapse>
    </Card>
  );
}
